"use client";

import "quill/dist/quill.snow.css";
import "../../../globals.css";
import React, { use, useEffect, useState, useRef } from "react";
import Sidebar from "../../../component/Sidebar";
import Header from "../../../component/Header";

export default function DocumentPage({ params }) {
  const { categoryId, documentId } = use(params);

  const [content, setContent] = useState("");
  const [sections, setSections] = useState([]);
  const [activeId, setActiveId] = useState(null);

  const contentRef = useRef(null);
  const mainRef = useRef(null);

  // üîπ Fetch sections
  useEffect(() => {
  if (!documentId) return;

  fetch(`http://localhost:5000/api/sections/document/${documentId}`)
    .then((res) => res.json())
    .then((data) => {
      if (data.success) {
        // ‚úÖ Karena backend kirim array (findAll), ambil dulu element pertama
        if (data.data && data.data.length > 0) {
          const html = data.data[0].content || "";

          const fixedHtml = html.replace(/<h2>(.*?)<\/h2>/g, (match, title) => {
            const plainTitle = title.replace(/<[^>]+>/g, "").trim();
            const id = plainTitle
              .toLowerCase()
              .replace(/[^a-z0-9]+/g, "-")
              .replace(/^-+|-+$/g, "");
            return `<h2 id="${id}">${title}</h2>`;
          });

          setContent(fixedHtml);
        } else {
          setContent("<p>Tidak ada konten untuk dokumen ini.</p>");
        }
      }
    })
    .catch((err) => console.error("Error fetching section:", err));
}, [documentId]);

  // üîπ Ambil daftar <h2>
  useEffect(() => {
    if (!contentRef.current) return;

    const observer = new MutationObserver(() => {
      const headings = contentRef.current.querySelectorAll("h2[id]");
      if (headings.length > 0) {
        const list = Array.from(headings).map((h) => ({
          id: h.id,
          title: h.textContent.trim(),
        }));
        setSections(list);
        observer.disconnect();
      }
    });

    observer.observe(contentRef.current, { childList: true, subtree: true });
    return () => observer.disconnect();
  }, [content]);

  // üîπ Highlight active heading
  useEffect(() => {
    if (!contentRef.current || !mainRef.current) return;

    const headings = contentRef.current.querySelectorAll("h2[id]");
    if (!headings.length) return;

    const observer = new IntersectionObserver(
      (entries) => {
        const visible = entries
          .filter((entry) => entry.isIntersecting)
          .sort((a, b) => a.boundingClientRect.top - b.boundingClientRect.top);

        if (visible.length > 0) {
          const nextActive = visible[0].target.id;
          if (nextActive !== activeId) setActiveId(nextActive);
        }
      },
      {
        root: mainRef.current,
        rootMargin: "0px 0px -50% 0px",
        threshold: 0.2,
      }
    );

    headings.forEach((h) => observer.observe(h));
    return () => observer.disconnect();
  }, [sections, activeId]);

  return (
    <div className="flex min-h-screen bg-white">
      {/* Header */}
      <Header />
      {/* Sidebar kiri */}
      <aside>
        <Sidebar />
      </aside>

      {/* Main content */}
      <main
        ref={mainRef}
        className={`flex-1 p-8 overflow-y-auto h-screen scroll-smooth ${
          sections.length === 0 ? "max-w-full" : "max-w-[calc(100%-16rem)]"
        }`}
      >
        <div
          ref={contentRef}
          className="prose max-w-none ql-editor"
          dangerouslySetInnerHTML={{ __html: content }}
        />
      </main>

      {/* Daftar Isi kanan, hanya tampil kalau ada h2 */}
      {sections.length > 0 && (
        <aside className="w-64 bg-gray-50 shadow-inner border-l border-gray-200 sticky top-0 h-screen overflow-y-auto">
          <div className="p-4 border-b border-gray-200">
            <h3 className="text-lg font-bold text-gray-800">Daftar Isi</h3>
          </div>
          <nav className="flex flex-col p-4 space-y-2">
            {sections.map((s) => (
              <a
                key={s.id}
                href={`#${s.id}`}
                className={`px-3 py-2 rounded-md transition scroll-smooth ${
                  activeId === s.id
                    ? "bg-blue-500 text-white font-semibold shadow-md"
                    : "text-gray-700 hover:bg-blue-100 hover:text-blue-800"
                }`}
              >
                {s.title}
              </a>
            ))}
          </nav>
        </aside>
      )}
    </div>
  );
}
